/**
 * ModalSavePattern - 
 * 
 */

import { Notice } from 'obsidian';

export interface SaveOptions {
  successMessage?: string;
  errorMessage?: string;
  onSuccess?: () => void;
  onError?: (error: Error) => void;
}

/**
 * 
 * @param saveAction 
 * @param options 
 * @returns 
 */
export function useSaveHandler(
  saveAction: () => Promise<void>,
  options: SaveOptions = {}
) {
  const {
    successMessage = '',
    errorMessage = '',
    onSuccess,
    onError
  } = options;
  
  return async () => {
    try {
      await saveAction();
      new Notice(` ${successMessage}`);
      onSuccess?.();
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error(String(error));
      const message = errorObj.message || '';
      new Notice(` ${errorMessage}: ${message}`);
      console.error(`${errorMessage}:`, errorObj);
      onError?.(errorObj);
    }
  };
}

/**
 * 
 * @param saveAction 
 * @param confirmMessage 
 * @param options 
 * @returns 
 */
export function useSaveHandlerWithConfirm(
  saveAction: () => Promise<void>,
  confirmMessage: string = '',
  options: SaveOptions = {}
) {
  const saveHandler = useSaveHandler(saveAction, options);
  
  return async () => {
    if (confirm(confirmMessage)) {
      await saveHandler();
    }
  };
}

/**
 * 
 * @param validateFn  null
 * @param saveAction 
 * @param options 
 * @returns 
 */
export function useSaveHandlerWithValidation<T>(
  validateFn: (data: T) => string | null,
  saveAction: (data: T) => Promise<void>,
  options: SaveOptions = {}
) {
  return async (data: T) => {
    const validationError = validateFn(data);
    if (validationError) {
      new Notice(` : ${validationError}`);
      return;
    }
    
    const saveHandler = useSaveHandler(() => saveAction(data), options);
    await saveHandler();
  };
}
