// src/features/dashboard/ui/ModuleSettingsModal.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
import { useMemo } from 'preact/hooks';
import { FormControlLabel, Checkbox } from '@mui/material';
import { VIEW_OPTIONS, ViewName, getAllFields } from '@/core/types/schema';
import type { ViewInstance } from '@/core/types/schema';
import { VIEW_EDITORS } from '@features/settings/registry';
import type { DataStore } from '@/core/services/DataStore';
import { useStore, AppStore } from '@/app/AppStore';
import { SimpleSelect } from '@shared/ui/composites/SimpleSelect';
import { RuleBuilder } from '@features/settings/RuleBuilder';
import { Modal } from '@shared/ui/primitives/Modal';
import { FormField, FieldManager, useSaveHandler } from '@shared/index';

//  - 
function ViewInstanceEditor({ vi, appStore, dataStore }: { vi: ViewInstance, appStore: AppStore, dataStore: DataStore }) {
    // storeviewInstance
    const currentVi = useStore(state => state.settings.viewInstances.find(v => v.id === vi.id)) || vi;
    const fieldOptions = useMemo(() => getAllFields(dataStore?.queryItems() || []), [dataStore]);
    const EditorComponent = VIEW_EDITORS[currentVi.viewType];

    const correctedViewConfig = useMemo(() => {
        if (currentVi.viewConfig && typeof (currentVi.viewConfig as any).categories === 'object') return currentVi.viewConfig;
        if (currentVi.viewConfig && (currentVi.viewConfig as any).viewConfig) return (currentVi.viewConfig as any).viewConfig;
        return currentVi.viewConfig || {};
    }, [currentVi.viewConfig]);

    const handleUpdate = (updates: Partial<ViewInstance>) => {
        appStore.updateViewInstance(currentVi.id, updates);
    };

    // 
    const viewTypeOptions = useMemo(() => 
        VIEW_OPTIONS.map(v => ({ value: v, label: v.replace('View', '') })), 
        []
    );


    // 
    const handleFieldsChange = (fields: string[]) => {
        handleUpdate({ fields });
    };

    return (
        <div>
            {/*  */}
            <div style={{ marginBottom: '2rem' }}>
                <h4 style={{ 
                    marginBottom: '1rem', 
                    color: 'var(--text-muted)',
                    fontSize: '1rem',
                    fontWeight: '600'
                }}>
                    
                </h4>
                
                <FormField label="">
                    <div style={{ display: 'flex', alignItems: 'center', gap: '1rem' }}>
                        <SimpleSelect 
                            value={currentVi.viewType} 
                            options={viewTypeOptions} 
                            onChange={val => handleUpdate({ viewType: val as ViewName })} 
                            sx={{ flex: 1, minWidth: '150px' }}
                        />
                        <FormControlLabel 
                            control={
                                <Checkbox 
                                    size="small" 
                                    checked={!!currentVi.collapsed} 
                                    onChange={e => handleUpdate({ collapsed: (e.target as HTMLInputElement).checked })} 
                                />
                            } 
                            label="" 
                        />
                    </div>
                </FormField>

                <FormField 
                    label="" 
                    help=""
                >
                    <FieldManager 
                        fields={currentVi.fields || []}
                        availableFields={fieldOptions}
                        onFieldsChange={handleFieldsChange}
                        placeholder="+ ..."
                    />
                </FormField>

            </div>

            {/*  */}
            <div style={{ 
                borderTop: '1px solid var(--background-modifier-border)', 
                paddingTop: '1.5rem', 
                marginBottom: '1.5rem' 
            }}>
                <h4 style={{ 
                    marginBottom: '1rem', 
                    color: 'var(--text-muted)',
                    fontSize: '1rem',
                    fontWeight: '600'
                }}>
                    
                </h4>
                
                <FormField 
                    label="" 
                    help=""
                >
                    <RuleBuilder 
                        title="" 
                        mode="filter" 
                        rows={currentVi.filters || []} 
                        fieldOptions={fieldOptions} 
                        onChange={(rows: any) => handleUpdate({ filters: rows })} 
                        dataStore={dataStore}
                    />
                </FormField>
                
                <FormField 
                    label="" 
                    help=""
                >
                    <RuleBuilder 
                        title="" 
                        mode="sort" 
                        rows={currentVi.sort || []} 
                        fieldOptions={fieldOptions} 
                        onChange={(rows: any) => handleUpdate({ sort: rows })} 
                        dataStore={dataStore}
                    />
                </FormField>
            </div>

            {/*  */}
            {EditorComponent && (
                <div style={{ 
                    borderTop: '1px solid var(--background-modifier-border)', 
                    paddingTop: '1.5rem' 
                }}>
                    <h4 style={{ 
                        marginBottom: '1rem', 
                        color: 'var(--text-muted)',
                        fontSize: '1rem',
                        fontWeight: '600'
                    }}>
                        {currentVi.viewType.replace('View', '')} 
                    </h4>
                    <EditorComponent 
                        module={currentVi} 
                        value={correctedViewConfig} 
                        onChange={(patch: any) => handleUpdate({ viewConfig: { ...correctedViewConfig, ...patch } })} 
                        fieldOptions={fieldOptions}
                        dataStore={dataStore}
                    />
                </div>
            )}
        </div>
    );
}

interface Props {
    isOpen: boolean;
    onClose: () => void;
    module: ViewInstance;
    appStore: AppStore;
    dataStore: DataStore;
}

export function ModuleSettingsModal({ isOpen, onClose, module, appStore, dataStore }: Props) {
    // store
    const currentModule = useStore(state => state.settings.viewInstances.find(v => v.id === module.id)) || module;

    // 
    const handleSave = useSaveHandler(async () => {
        //  ViewInstanceEditor  updateViewInstance
        // 
        await new Promise(resolve => setTimeout(resolve, 100));
        onClose();
    }, {
        successMessage: ` "${module.title}" `,
        errorMessage: ''
    });

    return (
        <Modal
            isOpen={isOpen}
            onClose={onClose}
            title={`: ${module.title}`}
            onSave={handleSave}
            saveButtonText=""
            size="large"
        >
            <ViewInstanceEditor vi={currentModule} appStore={appStore} dataStore={dataStore} />
        </Modal>
    );
}
