import type ThinkPlugin from '@main';
import { AppStore } from '@/app/AppStore';
import { DataStore } from '@core/services/DataStore';
import { RendererService } from '@/features/settings/RendererService';
import { ActionService } from '@core/services/ActionService';
import * as QuickInputFeature from '@features/quickinput';
import { setupSettings, setupDashboard } from '@/features/settings';

/**
 * FeatureLoader - UI 
 *  UI  (Dashboard, Settings, QuickInput )
 */
export class FeatureLoader {
    private plugin: ThinkPlugin;
    private appStore: AppStore;
    private dataStore: DataStore;
    private rendererService: RendererService;
    private actionService: ActionService;

    constructor(
        plugin: ThinkPlugin,
        appStore: AppStore,
        dataStore: DataStore,
        rendererService: RendererService,
        actionService: ActionService
    ) {
        this.plugin = plugin;
        this.appStore = appStore;
        this.dataStore = dataStore;
        this.rendererService = rendererService;
        this.actionService = actionService;
    }

    /**
     *  UI 
     * @param dataScanPromise  Promise Dashboard 
     */
    async loadFeatures(dataScanPromise: Promise<void> | null): Promise<void> {
        console.time('[ThinkPlugin] UI');
        
        // 1. Dashboard ()
        await this.loadDashboardFeature(dataScanPromise);
        
        // 2. Settings ()
        this.loadSettingsFeature();
        
        // 3. QuickInput ()
        this.loadQuickInputFeature();
        
        console.timeEnd('[ThinkPlugin] UI');
    }

    private async loadDashboardFeature(dataScanPromise: Promise<void> | null): Promise<void> {
        //  Dashboard 
        if (dataScanPromise) {
            await dataScanPromise;
        }
        
        console.time('[ThinkPlugin] Dashboard');
        setupDashboard?.({
            plugin: this.plugin,
            appStore: this.appStore,
            dataStore: this.dataStore,
            rendererService: this.rendererService,
            actionService: this.actionService,
        });
        console.timeEnd('[ThinkPlugin] Dashboard');
    }

    private loadSettingsFeature(): void {
        // 
        setTimeout(() => {
            console.time('[ThinkPlugin] Settings');
            
            setupSettings?.({
                app: this.plugin.app,
                plugin: this.plugin,
                appStore: this.appStore,
                dataStore: this.dataStore,
            });

            this.plugin.addCommand({
                id: 'think-open-settings',
                name: ' Think ',
                callback: () => {
                    // @ts-ignore
                    this.plugin.app.setting.open();
                    // @ts-ignore
                    this.plugin.app.setting.openTabById(this.plugin.manifest.id);
                }
            });

            console.timeEnd('[ThinkPlugin] Settings');
        }, 150);
    }

    private loadQuickInputFeature(): void {
        setTimeout(() => {
            console.time('[ThinkPlugin] QuickInput');
            QuickInputFeature.setup?.({
                plugin: this.plugin,
                appStore: this.appStore
            });
            console.timeEnd('[ThinkPlugin] QuickInput');
        }, 100);
    }
}
