// src/shared/components/TagsRenderer.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
import { useMemo } from 'preact/hooks';
import type { ThemeDefinition } from '@/core/types/schema';
import { getSimplifiedThemeDisplay } from '@core/utils/themeUtils';
import { getCategoryColor } from '@/core/types/definitions';

interface TagsRendererProps {
    tags: string[];
    allThemes: ThemeDefinition[];
}

export function TagsRenderer({ tags, allThemes }: TagsRendererProps) {
    //  useMemo  tags  allThemes 
    const { themeLabels, regularTags } = useMemo(() => {
        return getSimplifiedThemeDisplay(tags, allThemes);
    }, [tags, allThemes]);

    return (
        <div class="bv-fields-list">
            {/*  */}
            {themeLabels.map(({ fullPath, label }) => (
                <span 
                    key={fullPath}
                    class="tag-pill" 
                    title={`: ${fullPath}`}
                    // 
                    style={{ background: getCategoryColor(fullPath) }}
                >
                    {label}
                </span>
            ))}
            {/*  */}
            {regularTags.map(tag => (
                <span 
                    key={tag}
                    class="tag-pill" 
                    title={`: ${tag}`}
                >
                    {tag}
                </span>
            ))}
        </div>
    );
}
