// src/ui/styles/mui-theme.ts
// MUI

import { createTheme } from '@mui/material/styles';

// CSSMUI
export const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#7c3aed', // --interactive-accent
    },
    secondary: {
      main: '#6366f1', // 
    },
    background: {
      default: '#ffffff',
      paper: '#f8fafc',
    },
    text: {
      primary: '#1e293b',
      secondary: '#64748b',
    },
  },
  typography: {
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
  },
  components: {
    // CSS-in-JSCSS
    MuiPaper: {
      styleOverrides: {
        root: {
          backgroundColor: '#f8fafc',
          color: '#1e293b',
          // 
          '.theme-dark &': {
            backgroundColor: '#1e293b',
            color: '#f1f5f9',
          },
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
        },
      },
    },
    MuiTab: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          fontSize: '14px',
        },
      },
    },
  },
});
