// src/core/domain/schema.ts
import type { RecurrenceInfo } from '@core/utils/mark';

// [] 
export interface Groupable {
    id: string;
    parentId: string | null;
}

// []  -  dataSource
export type GroupType = 'viewInstance' | 'layout';
export interface Group extends Groupable {
    name: string;
    type: GroupType;
    // [] UI
    collapsed?: boolean;
}

// [] 
export interface ThinkSettings {
    // [] 
    groups: Group[];
    viewInstances: ViewInstance[];
    layouts: Layout[];
    inputSettings: InputSettings;
    // [] 
    floatingTimerEnabled: boolean;
    // [] 
    activeThemePaths?: string[];
}

export const DEFAULT_SETTINGS: ThinkSettings = {
    groups: [],
    viewInstances: [],
    layouts: [],
    inputSettings: { blocks: [], themes: [], overrides: [] },
    // [] 
    floatingTimerEnabled: true,
    // []
    activeThemePaths: [],
};


// ----- []  (Input Settings) ----- //
// ...  ...
export interface TemplateFieldOption {
    value: string;
    label?: string;
}
export interface TemplateField {
    id: string;
    key: string;
    label: string;
    type: 'text' | 'textarea' | 'date' | 'time' | 'select' | 'radio' | 'number' | 'rating';
    defaultValue?: string;
    options?: TemplateFieldOption[];
    min?: number;
    max?: number;
}
export interface BlockTemplate {
    id: string;
    name: string;
    fields: TemplateField[];
    outputTemplate: string;
    targetFile: string;
    appendUnderHeader?: string;
}
export interface ThemeDefinition {
    id: string;
    path: string;
    icon?: string;
}
export interface ThemeOverride {
    id: string;
    blockId: string;
    themeId: string;
    disabled?: boolean;
    fields?: TemplateField[];
    outputTemplate?: string;
    targetFile?: string;
    appendUnderHeader?: string;
}
export interface InputSettings {
    blocks: BlockTemplate[];
    themes: ThemeDefinition[];
    overrides: ThemeOverride[];
    categories?: string[];
}

// -----  () ----- //

// [MODIFIED]  HeatmapView
export const VIEW_OPTIONS = ['BlockView', 'TableView', 'ExcelView', 'TimelineView', 'StatisticsView', 'HeatmapView'] as const;
export type ViewName = typeof VIEW_OPTIONS[number];

// []  Groupable 
export interface ViewInstance extends Groupable {
    title: string;
    viewType: ViewName;
    dataSourceId?: string; // [] 
    collapsed?: boolean;
    fields?: string[];
    group?: string;
    viewConfig?: Record<string, any>;
    actions?: ActionConfig[];
    // [] 
    filters?: FilterRule[];
    sort?: SortRule[];
}

// []  Groupable 
export interface Layout extends Groupable {
    name: string;
    viewInstanceIds: string[];
    hideToolbar?: boolean;
    initialView?: string;
    initialDate?: string;
    initialDateFollowsNow?: boolean;
    isOverviewMode?: boolean; // [] 
    useFieldGranularity?: boolean; // [] 
    selectedThemes?: string[]; // [] 
    selectedCategories?: string[];
    displayMode?: 'list' | 'grid';
    gridConfig?: {
        columns?: number;
    };
}
// ...  ...
export interface ActionConfig {
    id: string;
    label: string;
    type: 'create_item';
    targetFile: string;
    template: string;
    promptedFields: {
        key: string;
        label: string;
        type: 'text' | 'textarea' | 'select';
        options?: string[];
    }[];
}
export interface FilterRule {
    field: string;
    op: '=' | '!=' | 'includes' | 'regex' | '>' | '<';
    value: any;
    logic?: 'and' | 'or'; // /
}
export interface SortRule {
    field: string;
    dir: 'asc' | 'desc';
}
export interface Item {
    id: string;
    title: string;
    content: string;
    type: 'task' | 'block';
    tags: string[];
    theme?: string;     // [] 
    categoryKey: string;
    recurrence: string;
    recurrenceInfo?: RecurrenceInfo;
    startISO?: string;
    endISO?: string;
    startMs?: number;
    endMs?: number;
    date?: string;
    dateMs?: number;
    dateSource?: 'done' | 'due' | 'scheduled' | 'start' | 'created' | 'end' | 'block';
    created: number;
    modified: number;
    filename?: string;
    header?: string;
    icon?: string;
    priority?: 'lowest' | 'low' | 'medium' | 'high' | 'highest';
    extra: Record<string, string | number | boolean>;
    createdDate?: string;
    scheduledDate?: string;
    startDate?: string;
    dueDate?: string;
    doneDate?: string;
    cancelledDate?: string;
    file?: {
        path: string;
        line?: number;
        basename?: string;
    };
    fileName?: string;
    startTime?: string; // [] time  startTime
    endTime?: string;   // []  endTime
    duration?: number;
    period?: string;
    rating?: number;
    pintu?: string;
    // [] 
    folder?: string;
    periodCount?: number;
    // [] 
    displayCount?: number;     // 
    levelCount?: number;       //  < displayCount
    countForLevel?: boolean;   // true
    manuallyEdited?: boolean;  // 
}

// [] theme
export const CORE_FIELDS = ['id', 'type', 'title', 'content', 'categoryKey', 'tags', 'theme', 'recurrence', 'icon', 'priority', 'date', 'header', 'startTime', 'endTime', 'duration', 'period', 'rating', 'pintu', 'folder', 'periodCount'] as const;

export type CoreField = typeof CORE_FIELDS[number];
export function getAllFields(items: Item[]): string[] {
    const set = new Set<string>(CORE_FIELDS as unknown as string[]);
    items.forEach(it => {
        Object.keys(it.extra || {}).forEach(k => set.add('extra.' + k));
        const f: any = (it as any).file;
        if (f && typeof f === 'object') Object.keys(f).forEach((k: string) => set.add('file.' + k));
    });
    return Array.from(set).sort();
}
export function readField(item: Item, field: string): any {
    if (field.startsWith('extra.')) return item.extra?.[field.slice(6)];
    if (field.startsWith('file.')) return (item as any).file?.[field.slice(5)];
    return (item as any)[field];
}
