import 'reflect-metadata';

/**
 *  reflect-metadata 
 *  tsyringe 
 */
export function ensureReflectMetadata(): void {
    if (typeof Reflect === 'undefined') {
        console.error('[ThinkPlugin] Reflect !');
        throw new Error('Reflect object is not available');
    }

    if (typeof Reflect.getOwnMetadata !== 'function') {
        console.error('[ThinkPlugin] Reflect.getOwnMetadata !');
        console.log('[ThinkPlugin]  Reflect :', Object.getOwnPropertyNames(Reflect));
        throw new Error('Reflect.getOwnMetadata is not a function - reflect-metadata polyfill not properly loaded');
    }

    // 
    // console.log('[ThinkPlugin] reflect-metadata ');
}
