/** @jsxImportSource preact */
import { h } from 'preact';
import { useState } from 'preact/hooks';
import { Button, Tooltip } from '@mui/material';
import ScannerIcon from '@mui/icons-material/Scanner';
import { ThemeScanDialog } from './ThemeScanDialog';
import type { 
    ScanResult, 
    ScanConfig, 
    ImportResult 
} from '@/core/theme-matrix/ThemeScanService';

/**
 * 
 */
export interface ThemeImportButtonProps {
    /**  */
    onScan: (config: ScanConfig) => Promise<ScanResult>;
    /**  */
    onImport: (themes: string[]) => Promise<ImportResult>;
    /**  */
    disabled?: boolean;
    /**  */
    children?: string;
    /**  */
    tooltip?: string;
}

/**
 * 
 */
export function ThemeImportButton({
    onScan,
    onImport,
    disabled = false,
    children = "",
    tooltip = ""
}: ThemeImportButtonProps) {
    const [dialogOpen, setDialogOpen] = useState(false);

    const handleClick = () => {
        setDialogOpen(true);
    };

    const handleClose = () => {
        setDialogOpen(false);
    };

    return (
        <>
            <Tooltip title={tooltip}>
                <Button
                    onClick={handleClick}
                    disabled={disabled}
                    variant="outlined"
                    size="small"
                    startIcon={<ScannerIcon />}
                >
                    {children}
                </Button>
            </Tooltip>

            <ThemeScanDialog
                open={dialogOpen}
                onClose={handleClose}
                onScan={onScan}
                onImport={onImport}
            />
        </>
    );
}
