// src/core/domain/fields.ts
/**
 * @file  (Field Registry) - 
 * ----------------------------------------------------------------
 *  (SSOT)
 * 
 * - key:  (e.g., 'categoryKey')
 * - label:  (e.g., '')
 * - type:  (e.g., 'date', 'tags', 'string')
 * - description: UI
 *
 * UI
 * 
 */

import { Item, readField } from './schema';

export interface FieldDefinition {
    key: string;
    label: string;
    type: 'string' | 'number' | 'date' | 'boolean' | 'tags' | 'icon' | 'custom';
    description?: string;
    // UI
    formatter?: (value: any, item: Item) => string;
}

// 
export const FIELD_REGISTRY: Record<string, FieldDefinition> = {
    // ---  ---
    title:         { key: 'title',         label: '',      type: 'string', description: '' },
    categoryKey:   { key: 'categoryKey',   label: '',      type: 'string', description: ' ( "/done")' },
    date:          { key: 'date',          label: '',      type: 'date',   description: '' },
    tags:          { key: 'tags',          label: '',      type: 'tags',   description: '', formatter: (v) => Array.isArray(v) ? v.join(', ') : '' },
    priority:      { key: 'priority',      label: '',    type: 'string' },
    icon:          { key: 'icon',          label: '',      type: 'icon' },
    period:        { key: 'period',        label: '',      type: 'string', description: '////' },
    time:          { key: 'time',          label: '',      type: 'string', description: ' HH:mm' },
    duration:      { key: 'duration',      label: '',      type: 'number', description: '' },
    rating:        { key: 'rating',        label: '',      type: 'number', description: '' },
    pintu:         { key: 'pintu',         label: '',      type: 'string', description: '' },
    // [] 
    folder:        { key: 'folder',        label: '',    type: 'string', description: '' },
    periodCount:   { key: 'periodCount',   label: '',    type: 'number', description: '/' },

    // ---  ---
    'file.path':     { key: 'file.path',     label: '', type: 'string' },
    'file.basename': { key: 'file.basename', label: '',    type: 'string' },
    'header':        { key: 'header',        label: '', type: 'string' },

    // ---  ---
    startISO: { key: 'startISO', label: '', type: 'date' },
    endISO:   { key: 'endISO',   label: '', type: 'date' },

    // ---  extra  () ---
    'extra.': { key: 'extra.', label: '', type: 'string', description: '' },
    'extra.': { key: 'extra.', label: '', type: 'string', description: 'GTD' },
};


/**
 * UI
 * @param items -  Item 
 * @returns 
 */
export function getAvailableFields(items: Item[]): FieldDefinition[] {
    const allFields = new Map<string, FieldDefinition>();
    const registeredLabels = new Set<string>();

    // 1. 
    Object.values(FIELD_REGISTRY).forEach(def => {
        allFields.set(def.key, def);
        registeredLabels.add(def.label);
    });

    // 2.  item extra 
    items.forEach(it => {
        Object.keys(it.extra || {}).forEach(key => {
            if (registeredLabels.has(key)) {
                return;
            }
            const fullKey = 'extra.' + key;
            if (!allFields.has(fullKey)) {
                allFields.set(fullKey, {
                    key: fullKey,
                    label: key,
                    type: typeof (it.extra as any)[key] === 'number' ? 'number' : 'string',
                });
            }
        });
    });

    return Array.from(allFields.values()).sort((a, b) => a.label.localeCompare(b.label, 'zh'));
}

/**
 * key(label)
 * @param key - key, e.g., 'categoryKey'
 * @returns , e.g., '', or the key itself if not found.
 */
export function getFieldLabel(key: string): string {
    if (FIELD_REGISTRY[key]) {
        return FIELD_REGISTRY[key].label;
    }
    if (key.startsWith('extra.')) {
        return key.slice(6); // 'extra.' -> ''
    }
    return key;
}
