// src/features/dashboard/ui/ModulePanel.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
// []  IconButton, Tooltip 
import { IconButton, Tooltip } from '@mui/material';
import IosShareIcon from '@mui/icons-material/IosShare';
import SettingsIcon from '@mui/icons-material/Settings';

//  Preact  Material-UI 
const AnyIconButton = IconButton as any;


interface ModulePanelProps {
    title: string;
    collapsed?: boolean;
    children: any;
    onActionClick?: () => void;
    onToggle?: (e: MouseEvent) => void;
    onExport?: () => void; // [] 
    onSettingsClick?: () => void; // [] 
}

export function ModulePanel({ title, collapsed, children, onActionClick, onToggle, onExport, onSettingsClick }: ModulePanelProps) {
    const onHeaderClick = (e: MouseEvent) => {
        if ((e.target as HTMLElement).closest('.module-header-actions')) {
            return;
        }
        onToggle?.(e);
    };

    return (
        <div class="think-module">
            <div class="module-header" onClick={onHeaderClick as any} title="/Ctrl/ + /">
                <span class="module-title">{title}</span>
                <div class="module-header-controls">
                    <div class="module-header-actions">
                        {/* []  */}
                        {onSettingsClick && (
                            <Tooltip title="">
                                <AnyIconButton
                                    size="small"
                                    onClick={(e: any) => {
                                        e.stopPropagation();
                                        onSettingsClick();
                                    }}
                                    sx={{ padding: '4px' }}
                                >
                                    <SettingsIcon sx={{ fontSize: '1rem' }} />
                                </AnyIconButton>
                            </Tooltip>
                        )}
                        {/* []  */}
                        {onExport && (
                            <Tooltip title=" Markdown">
                                <AnyIconButton
                                    size="small"
                                    onClick={(e: any) => {
                                        e.stopPropagation();
                                        onExport();
                                    }}
                                    sx={{ padding: '4px' }}
                                >
                                    <IosShareIcon sx={{ fontSize: '1rem' }} />
                                </AnyIconButton>
                            </Tooltip>
                        )}
                        <span 
                            class="module-action-plus" 
                            title=""
                            onClick={(e) => {
                                e.stopPropagation(); //  header  onClick
                                onActionClick?.();
                            }}
                        >
                            +
                        </span>
                    </div>
                    <div class="module-toggle">{collapsed ? '' : ''}</div>
                </div>
            </div>
            {!collapsed && <div class="module-content">{children}</div>}
        </div>
    );
}
